import { slideDown, slideToggle, slideUp } from './../tools/utilities/animation-tools';
import { responsiveMediaAbove } from './../settings/responsive';
import {
  CLASS_VISIBLE, CLASS_ACTIVE, CLASS_INITIALISED, CLASS_PENDING
} from './../tools/constants/classes';

// Attributes
const ATTR_CATEGORY_HANDLE = 'data-category-handle';

// Selectors
const SELECTOR_FAQ_SECTION = '[data-faq]';
const SELECTOR_CATEGORIES_NAV = '[data-faq-category-nav]';
const SELECTOR_CATEGORIES_DEFAULT = '[data-faq-category-default]';
const SELECTOR_CATEGORIES_DEFAULT_CONTENT = '[data-faq-category-default-content]';
const SELECTOR_CATEGORIES_BODY = '[data-faq-category-body]';
const SELECTOR_FAQ_BUTTON = '[data-faq-category-button]';
const SELECTOR_CARDS_CONTAINER = '[data-questions-container]';
const SELECTOR_ACCORDION = '[data-accordion]';
const SELECTOR_ACCORDION_TOGGLE = '[data-accordion-toggle]';
const SELECTOR_ACCORDION_BODY = '[data-accordion-body]';
const SELECTOR_ACCORDION_HIDE = '[data-accordion-hide]';
const SELECTOR_ACCORDION_SHOW = '[data-accordion-show]';

// Initialise sections
document.querySelectorAll<HTMLElement>(SELECTOR_FAQ_SECTION).forEach(section => {
  if(section.classList.contains(CLASS_INITIALISED)) return;
  section.classList.add(CLASS_INITIALISED);

  const categoriesNav = section.querySelector<HTMLElement>(SELECTOR_CATEGORIES_NAV);
  if(!categoriesNav) return;

  const categoriesDefault = categoriesNav.querySelector<HTMLButtonElement>(SELECTOR_CATEGORIES_DEFAULT);
  if(!categoriesDefault) return;

  const categoriesBody = categoriesNav.querySelector<HTMLElement>(SELECTOR_CATEGORIES_BODY);
  if(!categoriesBody) return;

  const accordions = section.querySelectorAll<HTMLElement>(SELECTOR_ACCORDION);
  if(!accordions) return;

  // Setup mobile categories toggle
  categoriesDefault.addEventListener('click', e => toggleDropdown());

  const categoryButtons = section.querySelectorAll<HTMLButtonElement>(SELECTOR_FAQ_BUTTON);
  const cardsContainer = section.querySelector<HTMLElement>(SELECTOR_CARDS_CONTAINER);
  if(!categoryButtons || !cardsContainer) return;

  // Setup buttons
  categoryButtons.forEach(button => {
    button.addEventListener('click', e => {
      let category = button.getAttribute(ATTR_CATEGORY_HANDLE);
      if(!category) return;

      //Shows only the questions in the selected category
      switchCategory(category);

      const defaultOption = categoriesNav.querySelector<HTMLElement>(SELECTOR_CATEGORIES_DEFAULT_CONTENT);
      if(!defaultOption) return;

      defaultOption.innerText = (e.target as HTMLElement).innerText;
      toggleDropdown();
    });
  });

  // Setup accordions
  accordions.forEach(accordion => {
    const accordionToggle = accordion.querySelector<HTMLElement>(SELECTOR_ACCORDION_TOGGLE);
    if(!accordionToggle) return;

    accordionToggle.addEventListener('click', e => toggleAnswer(accordion));
  });

  /**
   * Toggles the mobile categories dropdown
   */
  const toggleDropdown = () => {
    if(responsiveMediaAbove('MEDIUM')) return;

    if(categoriesDefault.classList.contains(CLASS_PENDING)) return;
    categoriesDefault.classList.add(CLASS_PENDING);

    const duration = 500;
    const toggleClass = () => categoriesNav.classList.toggle(CLASS_ACTIVE);

    // Handle the sliding
    if(window.getComputedStyle(categoriesBody).display === 'none') {
      toggleClass();
      setTimeout(() => categoriesDefault.classList.remove(CLASS_PENDING), duration);
      return slideDown(categoriesBody, duration);
    } else {
      setTimeout(() => {
        toggleClass();
        categoriesDefault.classList.remove(CLASS_PENDING);
      }, duration);
      return slideUp(categoriesBody, duration);
    }
  }

  /**
   * Sets the active category across buttons and accordions
   * @param activeCategory String used as the active category to switch to
   */
  const switchCategory = (activeCategory: string) => {
    // Update buttons class
    categoryButtons.forEach(button => {
      const buttonCategory = button.getAttribute(ATTR_CATEGORY_HANDLE);
      if(!buttonCategory) return;

      if(buttonCategory === activeCategory) {
        button.classList.add(CLASS_ACTIVE);
      } else {
        button.classList.remove(CLASS_ACTIVE);
      }
    });

    // Update accordions class
    accordions.forEach(accordion => {
      const accordionCategory = accordion.getAttribute(ATTR_CATEGORY_HANDLE);
      if(!accordionCategory) return;

      if(accordionCategory === activeCategory) {
        accordion.classList.add(CLASS_VISIBLE)
      } else {
        accordion.classList.remove(CLASS_VISIBLE);
      }
    });
  }

  /**
   * Toggles visible answers and the hide/show buttons in an element
   * @param accordion The html element that is toggled
   */
  const toggleAnswer = (accordion:HTMLElement) => {
    const hide = accordion.querySelector<HTMLElement>(SELECTOR_ACCORDION_HIDE);
    const show = accordion.querySelector<HTMLElement>(SELECTOR_ACCORDION_SHOW);
    const body = accordion.querySelector<HTMLElement>(SELECTOR_ACCORDION_BODY);

    if(!hide || !show || !body) return;
    [hide, show].forEach(el => el.classList.toggle(CLASS_VISIBLE));

    slideToggle(body);
  }
});
